@import '../../variables';

.validated-form-field {
 .validated-form-field__label {
   height: 16px;
 }

  .validated-form-field__error-message {
    height: 24px;
    color: @error-message-color;
    visibility: hidden;

    &_visible {
      visibility: visible;
    }
  }
}



